<template>
  <div class="love">
    <header
      class="love-header"
      ref="header"
    >
      <i
        class="iconfont icon-back"
        @click="$router.go(-1)"
      />
      <p class="title">
        我喜欢
      </p>
    </header>
    <div class="music-list-h" v-if="lovedList.length>0" @click="playMusic(0)">
      <!-- :style='sty' @click="playMusic(0)" v-show="songListData!=''" -->
      <i class="iconfont icon-start" />
      <p >全部播放<span>({{ lovedList.length }})</span></p>
    </div>
      <div v-else class="noting">
        什么都没有
      </div>
    <MusicList
      :musiclist="lovedList"
      class="lvoe-main"
    />
  </div>
</template>

<script>
import MusicList from '@/components/musicList'
import {mapState,mapMutations} from 'vuex'
export default {
  name: 'Love',
  props: ['musiclist'],
  data() {
    return {
    }
  },
  components:{
    MusicList
  },
  computed: {
    ...mapState('musicLoved',['lovedList'])
  },
  methods: {
     ...mapMutations('playList',['setPlayList']),
      playMusic(index){ //播放音乐，更新store中的播放列表，和序号
        let songList = this.lovedList
        if (songList.length>1) {
            this.setPlayList({songList,index})
        }
    },
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.love{
  position: absolute;
  top:0;
  bottom: 0;
  width: 100%;
  z-index: 10;
  background: #fff;
  .love-header{
    height: 80px;
    position: fixed;
    width: 100%;
    z-index:11;
    transition: opacity 1s;
    line-height: 80px;
    text-align: center;
    .title{
      color: #000;
      font-size: $primary-font-size;
    }
    .iconfont{
      color: #000;
      display: block;
      padding: 0 30px;
      font-size: 30px;
      position: absolute;     

    }
  }
  .music-list-h{
      position: absolute;
      top: 80px;
      height: 100px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      border-bottom: 1px solid #eee;
      width: 100%;
      background: #fff;
      .iconfont{
        color: $primary-color;
        font-size: 40px;
        width:100px;
        text-align: center;
      }
      p{
        color: #000;
        font-size: $primary-font-size;
      }
    }
    .lvoe-main{
      position: absolute;
      top: 180px;
      bottom: 110px;
      width: 100%;
      background: #fff;
      overflow: auto;
    }
     .noting{
        position: absolute;
         top: 120px;
         font-size: $primary-font-size;
         text-align: center;
         color: #000;
         width: 100%;
      }
}
</style>
